<template>
	<div>
		<headerNav title="登录" :showBackArrow="false"/>

		<div class="logo">
			<img src="../../../static/img/sale.jpg"/>
		</div>

		<div class="form">
			<Form ref="loginForm" :model="formData" :rules="ruleInline">
				<FormItem prop="username">
					<i-input type="text" v-model="formData.username" clearable size="large" placeholder="用户名">
						<Icon type="ios-person" slot="prepend"></Icon>
					</i-input>
				</FormItem>
				<FormItem prop="password">
					<i-input type="password" v-model="formData.password" clearable size="large" placeholder="密码">
						<Icon type="ios-lock-outline" slot="prepend"></Icon>
					</i-input>
				</FormItem>
				<FormItem>
					<Button type="error" size="large" @click="handleSubmit" long>登 陆</Button>
				</FormItem>
			</Form>

			<router-link to="/retrieve">
				<div style="float: right">忘记密码？</div>
			</router-link>
		</div>

		<div class="m-thirdpart">
			<p class="tit">
				<router-link to="/register">
					<span class="txt">没有账号？点我注册</span>
				</router-link>
			</p>
		</div>
	</div>
</template>

<script>
	import headerNav from '../../components/header/nav'
	import {mapActions} from 'vuex';

	export default {
		name: 'Login',

		components: {
			headerNav,
		},

		data() {
			return {
				formData: {
					username: '',
					password: ''
				},

				ruleInline: {
					username: [
						{required: true, message: '请输入用户名', trigger: 'blur'}
					],
					password: [
						{required: true, message: '请输入密码', trigger: 'blur'},
						{type: 'string', min: 6, message: '密码不能少于6位', trigger: 'blur'}
					]
				}
			}
		},

		methods: {
			...mapActions(['login']),

			handleSubmit() {
				this.$refs['loginForm'].validate(async (valid) => {
					if (valid) {
						let res = await this.login(this.formData);
						if (res) {
							this.$Message.success('登陆成功');
							this.$router.replace('/index');
						}
					} else {
						this.$Message.error('请填写正确的用户名或密码');
					}
				});
			}
		},
	}
</script>

<style>
	.logo {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.logo img {
		width: 50%;
		height: auto;
	}

	.form {
		padding-top: 10px;
		margin: auto;
		width: 80%;
	}

	.m-thirdpart {
		padding: 40px 20px;
		font-size: 12px;
	}

	.tit {
		position: relative;
		width: 70%;
		margin: 0 auto;
		border-bottom: 1px solid #eee;
	}

	.txt {
		position: absolute;
		left: 50%;
		top: -12px;
		margin: 0 0 0 -57px;
		width: 114px;
		background: #fff;
		text-align: center;
		color: #999;
		font-size: 12px;
		line-height: 26px;
	}
</style>



